import { mapState} from 'vuex';
export default {
	data() {
		return {
			confirm:false
		}
	},
	computed:{
		...mapState(["selectProduct"])
	},
	onLoad() {
		uni.setNavigationBarTitle({
		　　title:'设备配置'
		})
	},
	methods: {
		change(){
			this.confirm = true;
		},
		next(){
			if(this.confirm){
				uni.navigateTo({
					url:'./connectWifi'
				})
			}
		}
	}
}
